<template>
	<div class="homeLayer layer">
		<mo-navBar :nowPage="nowPage"></mo-navBar>
		<div class="main">
			<div class="freshWrap wrap">
				<p class="mainTitle">新鲜主题</p>
				<div class="lists">
					<div class="scroll">
						<div class="hover" style="background:url(./static/image/s01.jpg);background-size:cover;" v-tap="{ methods : navTo, id: '123' }">
							<p><i style="background:red;">#</i>主题内容</p>
						</div>
						<div style="background:url(./static/image/s02.jpg);background-size:cover;" v-tap="{ methods : navTo, id: '123' }">
							<p><i style="background:blue;">#</i>主题内容</p>
						</div>
						<div style="background:url(./static/image/s03.jpg);background-size:cover;" v-tap="{ methods : navTo, id: '123' }">
							<p><i style="background:orange;">#</i>主题内容</p>
						</div>
						<div style="background:url(./static/image/s05.jpg);background-size:cover;" v-tap="{ methods : navTo, id: '123' }">
							<p><i style="background:black;">#</i>主题内容</p>
						</div>
					</div>
				</div>
			</div>
			<div class="collectWrap wrap">
				<p class="mainTitle">精选内容</p>
				<div class="lists">
					<div class="scroll">
						<div v-tap="{ methods : navTo, id: '123' }">
							<div class="cover" style="background: url(./static/image/d01.jpg);background-size:cover;"></div>
							<div class="info">
								<div class="infoHd">
									<div class="avatar">
										<img src="../../static/image/avatar.png">
									</div>
									<p><i>#</i>内容标题</p>
								</div>
								<div class="infoBd">
									<p class="infoDesc">文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述文字描述</p>
								</div>
							</div>
						</div>
						<div v-tap="{ methods : navTo, id: '123' }">
							<div class="cover" style="background: url(./static/image/d01.jpg);background-size:cover;"></div>
							<div class="info">
								<div class="infoHd">
									<div class="avatar">
										<img src="../../static/image/avatar.png">
									</div>
									<p><i>#</i>内容标题</p>
								</div>
								<div class="infoBd">
									<p class="infoDesc">述文字描述文述文字描述文字描述</p>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import NavBar from './module/NavBar.vue'
	export default{
		name: 'Home',
		components: {
			'mo-navBar': NavBar
		},
		data(){
			return {
				nowPage: 'Home'
			}
		},
		methods: {
			navTo: function(result) {
				this.$router.push('/p/' + result.id);
			},
			choose: function(result) {
				console.log(result.keyCode);
				if (result.keyCode === 13) {
					alert('你按回车键了');
				}
			}
		},
		beforeRouteEnter: function(to, from, next) {
			console.log('beforeRouteEnter');
			next();
		},
		beforeRouteLeave: function(to, from, next) {
			console.log('beforeRouteLeave');
			next();
		},
		beforeDestroy () {
			console.log('beforeDestroy');
		},
		created: function(){
			console.log('created');
			var that = this;
			document.onkeydown = function(){
				that.choose();
			}
		},
		mounted() {
			console.log('mounted');
		},
		activated() {
			console.log('activated');
		},
		deactivated() {
			console.log('deactivated');
		}
	}
</script>

<style scoped>
	.main {
		position: absolute;
		width: 70vw;
		top: 0;
		bottom: 0;
		right: 5vw;
		text-align: left;
		font-size: 0;
	}
	.main .wrap {
		margin-bottom: 4vw;
	}
	.mainTitle {
		font-size: .48rem;
		color: rgba(0,0,0,.4);
		margin-bottom: .35rem;
		letter-spacing: .04rem;
	}
	.freshWrap {
		margin-top: 5vh;
	}
	.freshWrap .lists {
		width: 100%;
		font-size: 0;
	}
	.freshWrap .lists .scroll {
		width: 88.75vw;
	}
	.freshWrap .lists .scroll > div {
		position: relative;
		display: inline-block;
		width: 16.75vw;
		height: 13vw;
		margin-right: 1vw;
		text-align: center;
		letter-spacing: .04rem;
		border-radius: .06rem;
		cursor: pointer;
	}
	.freshWrap .lists .scroll > div:hover {
		position: relative;
		transform: scale(1.08, 1.08);
		z-index: 2;
	}
	.freshWrap .lists .scroll > div:hover:after {
		content: '';
		position: absolute;
		left: 10%;
		right: 10%;
		height: 30%;
		bottom: -15%;
		background: rgba(0,0,0,.5);
		filter: blur(18px);
		border-radius: 1rem;
		z-index: -1;
	}
	.freshWrap .lists .scroll > div:before {
		content: '';
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		height: 1rem;
		background: url(../assets/image/mask_bg.png) repeat-x;
		background-size: 1px 100%;
	}
	.freshWrap .lists .scroll > div p {
		position: absolute;
		left: .2rem;
		right: .2rem;
		bottom: .15rem;
		color: #FFF;
		font-size: .25rem;
		line-height: .34rem;
		vertical-align: middle;
		display: -webkit-box;
		overflow: hidden;
		text-overflow: ellipsis;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}
	.freshWrap .lists .scroll > div p i {
		display: inline-block;
		width: .3rem;
		height: .3rem;
		line-height: .3rem;
		font-size: .25rem;;
		background: red;
		border-radius: 5rem;
		font-style: normal;
		padding-left: .03rem;
		margin-right: .1rem;
	}
	.collectWrap .lists .scroll > div {
		position: relative;
		width: 100%;
		height: 14vw;
		overflow: hidden;
		background: rgba(255,255,255,.3);
		margin-bottom: .28rem;
		text-align: center;
		border-radius: .06rem;
	}
	.collectWrap .lists .scroll > div .cover {
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		width: 38%;
	}
	.collectWrap .lists .scroll > div .info {
		height: 100%;
		padding: 2vw 4vw 2vw 48%;
	}
	.collectWrap .infoHd {
		margin-bottom: .3rem;
	}
	.collectWrap .infoHd .avatar {
		display: inline-block;
		width: .9rem;
		height: .9rem;
		border-radius: 5rem;
		overflow: hidden;
		margin-right: .2rem;
		vertical-align: middle;
		font-size: 0;
	}
	.collectWrap .infoHd .avatar img {
		width: 100%;
	}
	.collectWrap .infoHd p {
		display: inline-block;
		vertical-align: middle;
		font-size: .4rem;
		letter-spacing: .05rem;
		font-weight: bold;
		vertical-align: middle;
	}
	.collectWrap .infoHd p i {
		font-style: normal;
		margin-right: .1rem;
		font-size: .5rem;
		vertical-align: middle;
	}
	.infoDesc {
	    font-size: .25rem;
		font-weight: bold;
		display: -webkit-box;
		overflow: hidden;
		text-overflow: ellipsis;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		letter-spacing: .03rem;
		text-align: left;
	}
</style>



