<template>
	<div class="navWrap">
		<div class="content">
			<p :class="nowPage=='Home' ? 'active icon-tj2' : 'icon-tj'" v-tap="{ methods:toNav, nav:'' }"></p>
			<p :class="nowPage=='Discovery' ? 'active icon-fx2' : 'icon-fx'" v-tap="{ methods:toNav, nav:'Discovery' }"></p>
			<p :class="nowPage=='Concern' ? 'active icon-dy2' : 'icon-dy'" v-tap="{ methods:toNav, nav:'Concern' }"></p>
		</div>
	</div>
</template>

<script>
export default{
	props: {
		nowPage: String
	},
	data() {
		return {
		}
	},
	methods: {
		toNav: function(result){
			this.$router.push('/' + result.nav);
		}
	}
}
</script>

<style scoped>
	.navWrap {
		display: -webkit-box;
		display: -webkit-flex;
		display: flex;
		position: fixed;
		width: 25vw;
		text-align: center;
		top: 0;
		left: 0;
		bottom: 0;
		-webkit-box-align: center;
		-webkit-align-items: center;
		align-items: center;
		-webkit-box-pack: center;
		-webkit-justify-content: center;
		justify-content: center;
		z-index: 1;
		flex: 1;
		flex-direction: row;
	}
	.navWrap .content p {
		position: relative;
		font-size: .4rem;
		margin: 7vh;
		cursor: pointer;
		padding: .07rem .35rem;
	}
	.navWrap .content p.active {
		background: url(../../assets/image/navCover.jpg) no-repeat;
		background-size: 100% 100%;
		border-radius: 5rem;
		color: #FFF;
	}
	.navWrap .content p.active:after {
		content: '';
		position: absolute;
		top: 20%;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(49,69,106,.9);
		border-radius: 5rem;
		z-index: -1;
		transform: scale(.95, .95);
		filter: blur(8px);
	}
</style>



